import React from 'react';
// import useResponsive from '../../shared/hooks/useResponsive'; //  default import  
import { useResponsive } from '../../shared/hooks/useResponsive'; // named import 
import MobileServices from './MobileServices';
import DesktopServices from './DesktopServices';

const Services = ({ onContactClick }) => {
  // const { isMobile } = useResponsive(); //     (   )
  const { isMobile } = useResponsive(); //       (       , import   )
  
  return isMobile ? (
    <MobileServices onContactClick={onContactClick} />
  ) : (
    <DesktopServices onContactClick={onContactClick} />
  );
};

export default Services; 